@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  font-family: "Source Code Pro", monospace;
}

html,
body,
#root {
  background-image: url(/furniture-archive/static/media/conc.567d7bee.jpg);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
}

h1 {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color: black;
}

.sticky{position:-webkit-sticky;position:sticky;top:0;z-index:100}*{box-sizing:border-box}body{margin:0px;color:black}.nav{padding:30px 60px 20px;width:100%;position:relative}.nav>.nav-header{display:inline}.nav>.nav-header>.nav-title{display:inline-block;font-size:1.4rem;padding:10px 10px 10px 10px}.nav>.nav-btn{display:none}.nav>.nav-links{display:inline;float:right;font-size:18px}.nav>.nav-links>a{display:inline-block;padding:13px 30px;text-decoration:none;color:black}.nav>.nav-links>a:hover{color:#969696}.nav>#nav-check{display:none}@media (max-width: 1000px){.nav{padding:30px 60px 20px;width:100%;position:relative}.nav>.nav-header>.nav-title{display:block;position:absolute;padding:0}.nav>.nav-btn{display:inline-block;position:absolute;right:0px;top:0px}.nav>.nav-btn>label{display:inline-block;position:absolute;width:50px;height:50px;padding:13px;right:20px;top:20px}.nav>.nav-btn>label>span{display:block;width:25px;height:10px;border-top:2px solid black}.nav>.nav-links{position:absolute;display:block;width:100%;background-color:rgba(0,0,0,0.3);height:0px;transition:all 0.3s ease-in;overflow-y:hidden;top:80px;left:0px}.nav>.nav-links>a{display:block;width:100%}.nav>#nav-check:not(:checked) ~ .nav-links{height:0px}.nav>#nav-check:checked ~ .nav-links{height:calc(100vh - 50px);overflow-y:auto}}

.card-container{height:50vh;width:450px;text-align:center}@media screen and (max-width: 450px){.card-container{width:90vw}}.canvas-container{height:80%}.text-container:hover{color:#969696}

#main-page{display:flex;justify-content:center}#main-page .container{margin:3vh auto;padding:10px;position:relative;align-self:center;display:inline-grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto auto;grid-column-gap:1vw;-webkit-column-gap:1vw;column-gap:1vw;grid-row-gap:10vh;row-gap:10vh}@media screen and (max-width: 1400px){#main-page .container{grid-template-columns:auto auto;grid-template-rows:auto auto}}@media screen and (max-width: 900px){#main-page .container{grid-column-gap:0;-webkit-column-gap:0;column-gap:0;grid-row-gap:5vh;row-gap:5vh;grid-template-columns:auto;grid-template-rows:auto}}.page-nav{display:flex;text-align:center;font-size:1.6rem}.page-nav div{margin:auto}

#about-page{display:flex;justify-content:center;flex:1 0 auto}#about-page .container{max-width:700px;margin:30vh auto;padding:10px;align-self:center}@media screen and (max-width: 900px){#about-page .container{width:80vw}}

#detail-page{display:flex;justify-content:center}#detail-page .container{margin:3vh auto;padding:10px;align-self:center;display:inline-grid;grid-template-columns:auto auto;grid-template-rows:auto auto;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:20px;row-gap:20px}@media screen and (max-width: 900px){#detail-page .container{grid-column-gap:0;-webkit-column-gap:0;column-gap:0;grid-template-columns:auto;grid-template-rows:auto}}#detail-page .canvas-container{width:500px;height:500px}@media screen and (max-width: 900px){#detail-page .canvas-container{margin:auto;width:90vw;height:40vh}}#detail-page .item-detail{width:40vw;max-width:500px}@media screen and (max-width: 900px){#detail-page .item-detail{width:90vw;margin:0 auto}}

.footer{text-align:center;height:100px;bottom:0px;left:0px;right:0px;margin-bottom:20px}

